.portfolio-page {

    background: linear-gradient(217deg, #056961d3, rgba(255, 0, 0, 0) 35%),
    linear-gradient(127deg, #4f0606, rgba(0,255,0,0) 0%),
    linear-gradient(336deg, #7b054ebd, rgba(0,0,255,0) 50%);

    padding-left: 100px;
    padding-right: 50px;
    width: calc(100% - 150px);
    height: 100%;
    overflow: auto;
    bottom: 100px;

    .text-zone {
        width: 80vw;
    }

    .images-container {
        display: flex;
        gap: 15px;
        flex-wrap: wrap;
        padding-bottom: 100px;
        animation: bounceIn 5s 2s backwards;

        .image-box {
            position: relative;
            flex: 1 1 20%;
            height: 250px;
            width: 300px;
            overflow: hidden;
            border-radius: 1rem;
            max-width: calc(25% - 10px);

            .portfolio-image {
                position: absolute;
                z-index: 2;
                width: 100%;
                height: 100%;
                object-fit: cover;
            }

            .content {
                position: absolute;
                width: 100%;
                z-index: 3;
                padding: 5px 10px;
                transition: all 0.5s cubic-bezier(0.645, 0.045, 0.355, 1);
                background: linear-gradient(
                    180deg,
                    #4a0606,
                    #bd9494d7
                );
                bottom: 5px;
            }

            .title {
                margin-bottom: 0;
                margin-top: 0;
                color: ivory;
                font-size: 2rem;
                font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
                font-weight: 400;
                line-height: 20px;
            }

            .description {
                font-size: 1.2rem;
                font-family: 'Courier New', Courier, monospace;
                margin-bottom: 5px;
                color: #ffd700;
                font-weight: 500;
            }

            .btn {
                margin-top: 10px;
                margin-bottom: 5px;
                padding: 0 23px;
                line-height: 15px;
                border-radius: 1rem;
                text-transform: uppercase;
                font-size: 1rem;
                color: #ffd700;
                background: #740707;
                transition: all 0.5s cubic-bezier(0.645, 0.045, 0.355, 1);
                cursor: pointer;
            }

            .btn:hover {
                transform: translateY(-3px);
                background: #0ad4b66f;
                color: black;
                font-size: 1.2rem;
            }

            &::after {
                content: "";
                background: linear-gradient(180deg, #ffd90080, rgba(0, 0, 0, 0.598));
                position: absolute;
                left: 0;
                right: 0;
                bottom: 0;
                top: 0;
                width: 100%;
                height: 100%;
                transition: all 0.5s cubic-bezier(0.645, 0.045, 0.355, 1);
                z-index: 2;
                opacity: 0;
            }

            &:hover::after{
                opacity: 0.85;
            }

            &:hover .content {
                bottom: 0;
            }
        }
    }
}

@media screen and (max-width: 1200px) {

    .portfolio-page {
        display: initial; 
        padding-left: 0;
        padding-right: 0;
        width: 100%;

            .text-zone {
            display: initial;
            padding-left: 10px;
            width: 100%;
            padding: 20px;
            box-sizing: border-box;

            h1.page-title {
                margin-left: 20px;
                margin-top: 20px;
            }

            .image-box {
                height: 200px;
                max-width: calc(50% - 10px);
            }

            .content {
                position: initial;
                max-width: 100%;
                max-height: 40%;
                bottom: 50%;

                .title, .description, .btn {
                    font-size: 5px;
                }
            }
        }
    }
}