html {
  font-size: 60%;
  background-color: #13294B !important;
}

body {
  margin: 0;
  font: 300 11px/1.4 'Monaco', 'Fjalla One';
  color: #4a0606;
  overflow: hidden;
  display: block;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


@media screen and (max-width: 1200px){
  body {
    overflow: visible;
  }
}